@import "../base/variables.less";
@import "../base/fonts.less";
@import "../base/viewports.less";

.navigation__list {
  background-color: @blue;
  color: @primary-color;
  font-size: 18px;
  font-weight: 700;
  display: flex;
  flex-direction: column;
}

.navigation__item {
  display: block;
  text-align: center;
  padding: 17px 78px;
}

.navigation__item:nth-child(2) {
  margin-right: auto;
}

.navigation__item:nth-child(3) {
  margin-left: auto;
}

@media (min-width: @tablet-width) {
  .navigation__list {
    flex-direction: row;
  }

  .navigation__item {
    min-width: 153px;
    padding: 18px 24px;
  }

  .navigation__toggle {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .navigation__item {
    min-width: 240px;
    padding: 18px 68px;
  }
}
